<template>
  <div>
    <h1>상세 내용</h1>
    {{ movie }}
  </div>
</template>

<script>
export default {
  created() {
    const id = this.$route.params.id;
    // eslint-disable-next-line arrow-parens
    this.$http.get(`/api/movies/${id}`).then(response => {
      this.movie = response.data;
    });
  },
  data() {
    return {
      movie: {},
    };
  },
};
</script>



