import Vue from 'vue';
import Router from 'vue-router';

import index from '@/view';
import login from '@/view/login';
import MovieIndexPage from '@/view/movie';
import MovieShowPage from '@/view/movieDetail';

Vue.use(Router);


export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
    },
    {
      path: '/login',
      name: 'login',
      component: login,
    },
    {
      path: '/movie',
      name: 'MovieIndexPage',
      component: MovieIndexPage,
    },
    {
      path: '/movie/:id',
      name: 'MovieShowPage',
      component: MovieShowPage,
    },
  ],
});



